import indexListItem from "../index/index-list-item.vue"
import {mapState} from "vuex"
export default{
	name:"zy-search",
	props:{
		isFocus:{	//是否自动获取焦点
			type:Boolean,
			value:false
		},
		showWant:{	//是否展示推荐菜单
			type:Boolean,
			value:true
		}
	},
	data() {
		return {
			searchText:'',								//搜索关键词
			hList:uni.getStorageSync('search_cache')|| [],		//历史记录
			wantList:['栏目1','栏目2','栏目3','栏目4']	//初始化推荐列表
		};
	},
	computed:{
		...mapState(['search'])
	},
	methods: {
		searchStart: function() {	//触发搜索
			if (this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			this.saveRecord() // 保存搜索记录
			this.$store.dispatch('getSearch',this.searchText) // 调用搜索接口
			
		},
		saveRecord(){
			console.log('saveRecord');
			uni.getStorage({
				key:'search_cache',
				success:(res) => {
					console.log(res,123);
					let list = res.data;
					let count = 0
					for (let item of list) {
						if(item === this.searchText){
							list.splice(count,1)
						}
						count++
					}
					list.unshift(this.searchText)
					this.hList = list;
					console.log(this.hList );
					uni.setStorage({
						key: 'search_cache',
						data: this.hList
					});
				},
				fail:(err) => {
					this.hList.unshift(this.searchText)
					uni.setStorage({
						key: 'search_cache',
						data: this.hList
					});
				}
			})
		},
		keywordsClick (item) {	//推荐搜索
			this.searchText = item;
		},
		delhistory (index) {		//清空历史记录 -1 b表示清空全部
			if(index >= 0){
				this.hList.splice(index,1)
			}else{
				this.hList = []
			}
			uni.setStorage({
				key: 'search_cache',
				data: this.hList
			});
		}	
	},
	watch:{
		search(newVal){
			if(newVal === -1){
				uni.showToast({
					title:'没有搜索到任何内容',
					icon: 'none',
					duration: 1000
				})
			}
			
		}
	},
	onReady(){ //页面加载时清空上次搜索队列里面的内容
		this.$store.dispatch('claerSearch')
	},
	components:{
		indexListItem
	}
}
